.stores-page h1 {
  text-align: center;
  margin: 20px;
}

.stores-page h1 + button {
  display: block;
  margin: 0 auto 20px;
  display: block;
  width: 100px;
  padding: 8px;
  color: white;
  background-color: #2c3e50;
  border: none;
  outline: none;
  border-radius: 5px;
  cursor: pointer;
}